@import 'assets/scss/variables.scss';

.container {
    display: flex;
    height: 100vh;
    width: 100%;
    background-color: #373737;
    align-items: center;
    flex-direction: column;
    overflow: auto;
    .inner-wrapper {
        padding: 5rem;
        display: flex;
        flex-direction: column;
        align-items: center;

        .logo-wrapper {
            margin-bottom: 3.5rem;
            text-align: center;
            font-family: 'Roboto', sans-serif;
            font-size: 1.5rem;
            @include md {
                margin-bottom: 1.5rem;
            }
            .logo {
                height: 20rem;
                margin-bottom: 3.5rem;
                @include xl {
                    height: 12.5rem;
                }
            }

            h3 {
                font-size: 5rem;
                font-family: 'Marcellus', serif;
                margin-bottom: 3.5rem;
                color: #fff;
                display: flex;
                @include xl {
                    font-size: 3rem;
                    margin-bottom: 1.5rem;
                }
            }
        }
    }

    .dot-wrapper {
        margin-left: 1rem;
        @include lg {
            display: none;
        }
        .dot {
            display: inline-block;
            width: 1rem;
            height: 1rem;
            border-radius: 50%;
            background-color: #fff;
            margin-right: 0.75rem;
            @include xl {
                font-size: 3rem;
                width: 0.5rem;
                height: 0.5rem;
            }
            &:nth-child(1) {
                animation: jumpingAnimation 1.2s 0.6s linear infinite;
            }
            &:nth-child(2) {
                animation: jumpingAnimation 1.2s 0.3s linear infinite;
            }
            &:nth-child(3) {
                animation: jumpingAnimation 1.2s 0s linear infinite;
            }
        }
    }
}

@keyframes jumpingAnimation {
    0% {
        transform: translate(0, 0);
    }
    16% {
        transform: translate(0, -15px);
    }
    33% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(0, 0);
    }
}
