@import 'assets/scss/variables.scss';

.wrapper {
    & :global(.d-flex) {
        @include sm {
            flex-direction: column;
        } 
    }
    & .time {
        & h1 {
            font-size: 10rem;
            color: #fff;
            background-color: #282828;
            padding: 2rem 1rem;
            margin: 0 1rem;
            border-radius: 2rem;
            min-width: 20rem;
            text-align: center;
            @include xl {
                font-size: 5rem;
                min-width: 10rem;
            }
            @include md {
                min-width: 8rem;
                font-size: 4.5rem;
                background-color: transparent;
                color: #fff;
                padding: 0;
            }
        }
        & span {
            font-family: 'Courier New', Courier, monospace;
            display: block;
            width: 100%;
            text-align: center;
            font-size: 3.5rem;
            margin-top: 3rem;
            color: rgb(255 255 255 / 50%);
            @include sm {
                margin: 1.5rem 0;
            } 
        }
    }
}