@import 'assets/scss/variables.scss';

table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
    font-size: 3rem;
    margin-top: 3rem;
    color: #fff;
  }
  
  td, th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
  }
  
  tr:nth-child(even) {
    background-color: #ddddddb8;
    color: #100f0f;
  }